<template>
	<loan-check @loadList="loadList" :data="data" :nodeStatus="nodeStatus"></loan-check>
</template>
<script>
	import loanCheck from './loancheck.vue'
	export default {
		data () {
			return {
				data:{},
				nodeStatus:1003
			}
		},
		components:{
			loanCheck
		},
		methods: {
			loadList: function (searchParams) {
				let _this = this;
				let params=searchParams.params;
				_this.$Message.config({
					top: 200
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				if(searchParams.tab == 'loan'){//放款初审=>当前节点flowStatus为0的
					params.flowStatus=0;
					params.nodeStatus=_this.nodeStatus;
				}else if(searchParams.tab == 'history'){
					params.nodeStatus=_this.nodeStatus;
					params.loanId=1;
				}
				this.$http.post('loanManage/loanCommon',params)
					.then(function (response) {
						var res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == 1) {//正常
							_this.$Message.success({
								content: res.msg
							});
							if(res.data.list){
								_this.data=res.data;
							}
						} else {
							_this.$Message.error(res.msg);
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Message.error({
							content: error
						});
					});
			}
		}
	}
</script>
<style>

</style>


